* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.list-item-card {
  padding: 40px 20px;
  border-radius: 13px;
  background-color: rgba(22, 22, 22, 0.8);
}
@media (max-width: 600px) {
  .list-item-card {
    padding: 30px 10px;
  }
}
.body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
.header {
  width: 100%;
  background-color: transparent;
}
.header__menu-bg--open {
  z-index: 1;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.header__wrap {
  width: 100%;
  height: 90px;
  z-index: 2;
  padding: 0 20px;
  position: fixed;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid #999999;
  background-color: #ffffff;
  transition: 0.5s all ease-in;
  color: #000000;
}
.header__inners {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
}
.header__logo {
  z-index: 2;
}
.header__back-btn {
  width: min-content;
  background-color: transparent;
  border: none;
  justify-self: end;
  width: 45px;
  height: 40px;
  cursor: pointer;
}
.header__back-icon {
  transform: rotate(180deg);
}
.main {
  width: 100%;
  height: 100%;
}
.footer {
  width: 100%;
  padding: 50px 20px;
  background: #F6F6F8;
}
.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
}
@media (max-width: 600px) {
  .footer__wrap {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}
.footer__info {
  width: 260px;
  position: relative;
}
@media (max-width: 600px) {
  .footer__info--adress {
    width: 100%;
  }
}
.footer__info--map {
  flex-grow: 1;
  max-width: none;
  width: 100%;
  height: 250px;
  background-image: url('../images/footer__map-bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.footer__no-js-map {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 1;
}
.footer__map {
  display: flex;
  width: 100%;
  height: 250px;
}
.footer__title {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
  margin-bottom: 10px;
}
@media (max-width: 900px) {
  .footer__title {
    text-align: center;
  }
}
.footer__sub-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #000000;
  margin-bottom: 10px;
}
@media (max-width: 900px) {
  .footer__sub-title {
    text-align: center;
  }
}
.footer__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #999999;
}
.footer__text--legal-info {
  margin-top: 8px ;
}
@media (max-width: 900px) {
  .footer__text {
    text-align: center;
  }
}
.footer__adress {
  display: grid;
  grid-template-columns: 1fr;
}
.footer__adress-icon {
  height: 20px;
  width: 20px;
}
.footer__adress-link {
  color: #000000;
  text-decoration: underline;
  cursor: pointer;
  padding: 8px 0;
  white-space: nowrap;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 6px;
}
@media (max-width: 900px) {
  .footer__adress-link {
    justify-content: center;
  }
}
.footer__adress-link > .footer__adress-icon {
  fill: #999999;
}
.footer__adress-link:hover,
.footer__adress-link:focus {
  color: #0068ab;
}
.footer__adress-link:hover > .footer__adress-icon,
.footer__adress-link:focus > .footer__adress-icon {
  fill: #0068ab;
}
.footer__rights {
  width: 100%;
  height: 100%;
  flex-grow: 1;
  padding: 20px 20px;
  margin-top: 20px;
  border-top: 1px solid #E0E0E0;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 21px;
  color: #999999;
}
.nav {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(7, auto);
  justify-content: end;
  grid-column-gap: 30px;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav {
    display: none;
  }
}
.nav--open {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: min-content;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(6, auto);
  grid-row-gap: 20px;
  padding: 90px 0 40px 0;
  border-bottom: 1px solid #000000;
  background-color: #ffffff;
  color: #000000;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}
.nav--open ~ .nav-button .ham .line {
  color: #000000;
}
.nav__item {
  height: 100%;
  list-style: none;
}
.nav__link {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Rubik', sans-serif;
  color: currentColor;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}
.nav__link--current {
  pointer-events: none;
}
.nav__link:hover,
.nav__link:focus,
.nav__link--current {
  color: #0068ab;
}
.nav__link:hover::after,
.nav__link:focus::after,
.nav__link--current::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 107px;
  height: 3px;
  background-color: #0068ab;
}
.nav-button {
  display: none;
  width: min-content;
  height: min-content;
  border: none;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav-button {
    z-index: 2;
    display: block;
    justify-self: end;
  }
}
.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.ham--black > .line {
  stroke: #000000;
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #fff;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.hamRotate.active {
  transform: rotate(45deg);
}
.hamRotate.active > .line {
  stroke: #000000;
}
.ham8 .top {
  stroke-dasharray: 40 160;
}
.ham8 .middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.ham8 .bottom {
  stroke-dasharray: 40 85;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
.ham8.active .top {
  stroke-dashoffset: -64px;
}
.ham8.active .middle {
  transform: rotate(90deg);
}
.ham8.active .bottom {
  stroke-dashoffset: -64px;
}
.cover {
  width: 100%;
  height: 100vh;
  min-height: 600px;
  padding: 80px 20px;
  background-image: url('../images/cover.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.cover__title {
  font-family: 'Rubik', sans-serif;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 600px) {
  .cover__title {
    font-weight: 600;
    font-size: 38px;
    line-height: 44px;
  }
}
.cover__link {
  width: 200px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0068ab;
  box-shadow: 0px 0px 10px rgba(111, 111, 111, 0.25);
  border-radius: 30px;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}
.cover__link:hover,
.cover__link:focus {
  transition: 0.3s all ease-in;
  background-color: #0087de;
  box-shadow: 8px 8px 20px #bebebe, -8px -8px 20px #ffffff;
}
.skills {
  width: 100%;
  padding: 100px 20px;
}
@media (max-width: 900px) {
  .skills {
    padding: 80px 20px;
  }
}
.skills__wrap {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-row-gap: 20px;
  align-content: center;
  justify-self: center;
}
.skills__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  text-align: center;
  color: #323232;
}
@media (max-width: 900px) {
  .skills__title {
    font-size: 30px;
    line-height: 33px;
  }
}
.skills__subtitle {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  color: #999999;
}
.skills__list {
  width: 100%;
  padding: 50px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
}
@media (max-width: 900px) {
  .skills__list {
    padding: 10px 0 0 0;
  }
}
.skills__item {
  width: 200px;
  position: relative;
  list-style: none;
  display: grid;
  grid-template-columns: 40px 1fr;
  grid-template-rows: minmax(40px) 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 5px;
  background-repeat: no-repeat;
  background-size: 40px 40px;
  background-position: top 0% left 0%;
}
