.skills__skill {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
  grid-column-start: 2;
  grid-column-end: -2;
}
.skills__text {
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
  grid-column-start: 1;
  grid-column-end: -1;
}
.skills__link {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #000000;
  font-family: 'Rubik', sans-serif;
  text-decoration-line: underline;
}
.skills__link:hover,
.skills__link:focus {
  color: #0068ab;
}
.about-us {
  width: 100%;
  padding: 100px 20px;
  background: #F6F6F8;
}
@media (max-width: 900px) {
  .about-us {
    padding: 80px 20px;
  }
}
.about-us__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr min-content;
  grid-template-rows: min-content min-content;
  grid-template-areas: "lf lf hs" "tf tf hs" "pl pl hs";
  grid-column-gap: 50px;
  grid-row-gap: 20px;
}
@media (max-width: 900px) {
  .about-us__wrap {
    grid-template-areas: "hs hs hs" "lf lf lf" "tf tf tf" "pl pl pl";
  }
}
.about-us__info {
  position: relative;
  grid-area: lf;
  height: min-content;
  display: grid;
  grid-template-rows: min-content min-content;
  grid-row-gap: 20px;
}
.about-us__history {
  grid-area: hs;
  display: grid;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;
  padding: 40px 20px;
  background-color: #0068ab;
  color: #ffffff;
  box-shadow: 14px 14px 28px #929292, -14px -14px 28px #f5f5f5;
  border-radius: 30px;
}
@media (max-width: 900px) {
  .about-us__history {
    padding: 20px;
    grid-template-areas: "tl tl tl" "tx tx im";
    grid-row-gap: 10px;
    grid-column-gap: 10px;
  }
}
@media (max-width: 900px) {
  .about-us__history {
    display: block;
  }
}
.about-us__mission {
  grid-area: tf;
}
.about-us__politic {
  grid-area: pl;
}
.about-us__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #323232;
}
.about-us__title--history {
  color: #ffffff;
}
@media (max-width: 900px) {
  .about-us__title--history {
    grid-area: tl;
  }
}
.about-us__text {
  font-family: 'Rubik', sans-serif;
  font-size: 16px;
  line-height: 25px;
  color: #000000;
}
.about-us__img {
  width: 200px;
  height: 200px;
  background-color: #000000;
}
@media (max-width: 900px) {
  .about-us__img {
    grid-area: im;
  }
}
@media (max-width: 900px) {
  .about-us__img {
    width: 150px;
    height: 150px;
    float: left;
    margin: 0 8px 0 0;
  }
}
.about-us__quotes {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}
@media (max-width: 900px) {
  .about-us__quotes {
    grid-area: tx;
  }
}
.about-us__quotes-autor {
  font-family: 'Open Sans', sans-serif;
  text-transform: initial;
  display: block ;
  margin-top: 10px;
}
.about-us__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
}
.about-us__list--mission {
  padding-left: 20px;
  grid-row-gap: 10px;
}
.about-us__list--quality {
  margin-left: 20px;
  margin-top: 8px;
  grid-row-gap: 7px;
}
.systems {
  width: 100%;
  padding: 100px 20px;
  background-image: url('../images/systems/systems__background.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
@media (max-width: 900px) {
  .systems {
    padding: 80px 10px;
  }
}
.systems__wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 30px;
}
.systems__titles {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 600px) {
  .systems__titles {
    justify-content: center;
  }
}
.systems__title {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  color: #ffffff;
  font-family: 'Rubik', sans-serif;
}
@media (max-width: 600px) {
  .systems__title {
    margin-bottom: 20px;
  }
}
.sys-list {
  width: 100%;
  padding-bottom: 40px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 40px;
  list-style: none;
  border-bottom: 1px solid #E0E0E0;
}
@media (max-width: 900px) {
  .sys-list {
    grid-template-columns: 1fr;
    border-bottom: none;
  }
}
.sys-list__item {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, min-content);
  grid-row-gap: 15px;
}
@media (max-width: 900px) {
  .sys-list__item {
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 20px;
    grid-template-areas: "title title title" "img text text" "img text text" "img link link";
  }
}
@media (max-width: 600px) {
  .sys-list__item {
    width: 100%;
    grid-template-columns: 1fr;
    grid-column-gap: 20px;
    grid-template-areas: "title" "img" "text" "link";
  }
}
.sys-list__img {
  display: flex;
  width: 100%;
  height: 150px;
  object-fit: cover;
}
.sys-list__image-link {
  border-radius: 13px;
  width: 100%;
}
@media (max-width: 900px) {
  .sys-list__image-link {
    grid-area: img;
  }
}
.sys-list__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 22px;
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
}
@media (max-width: 900px) {
  .sys-list__title {
    grid-area: title;
  }
}
.sys-list__text {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 25px;
  color: #ffffff;
  font-family: 'Open Sans', sans-serif;
}
@media (max-width: 900px) {
  .sys-list__text {
    grid-area: text;
  }
}
.sys-list__link {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  font-family: 'Open Sans', sans-serif;
  text-decoration-line: underline;
  color: #ffffff;
}
.sys-list__link:hover,
.sys-list__link:focus {
  color: #0068ab;
}
@media (max-width: 900px) {
  .sys-list__link {
    grid-area: link;
  }
}
.reference {
  width: 100%;
  background-color: #ffffff;
  padding: 100px 20px;
  background-image: url('../images/reference__background-img--foo.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.reference__wrap {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 40px;
}
.reference__title {
  font-weight: bold;
  font-size: 36px;
  line-height: 53px;
  text-align: center;
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
}
.reference__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  list-style: none;
}
@media (max-width: 900px) {
  .reference__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, min-content);
  }
}
@media (max-width: 600px) {
  .reference__list {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
  }
}
.reference__item {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  gap: 10px;
}
.reference__firm {
  order: 1;
  font-family: 'Rubik', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 25px;
  color: #ffffff;
}
.reference__text {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  text-align: center;
  color: #ffffff;
}
.reference__logo {
  align-self: center;
  width: 140px;
  height: 80px;
  object-fit: contain;
  background-color: transparent;
  display: flex;
}
.services {
  width: 100%;
  padding: 100px 20px;
  background-color: #161616;
}
.services__title {
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
  text-align: center;
  font-size: 32px;
}
.services__item {
  position: relative;
  padding: 30px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.services__item:nth-child(odd) {
  flex-direction: row-reverse;
  text-align: right;
}
.services__text {
  width: 70%;
}
.services__service-title {
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
  font-size: 28px;
}
.services__service-desription {
  font-family: 'Open Sans', sans-serif;
  color: #ffffff;
  font-size: 17px;
}
.services__img-wrap {
  height: 100%;
  width: 1px;
  overflow: hidden;
  opacity: 0;
  filter: grayscale(0.4);
}
.services__img-wrap--left {
  transform: rotate(180deg);
}
.services__img-wrap--active {
  height: min-content;
  width: 300px;
  opacity: 1;
  transition: opacity 0s, width 2s,;
}
@media (max-width: 900px) {
  .services__img-wrap--active {
    width: 200px;
  }
}
@media (max-width: 600px) {
  .services__img-wrap--active {
    width: 100px;
  }
}
.services__img-wrap--active > .services__image {
  transform: scale(1);
  transition: 1s ease-out;
}
.services__image {
  width: 300px;
  object-fit: contain;
}
@media (max-width: 900px) {
  .services__image {
    width: 200px;
  }
}
@media (max-width: 600px) {
  .services__image {
    width: 100px;
  }
}
.sys-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
}
.sys-cover__wrap {
  padding: 120px 20px 100px 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 40px;
}
.sys-cover__title {
  font-family: 'Rubik', sans-serif;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
  text-align: center;
  color: #000000;
}
@media (max-width: 600px) {
  .sys-cover__title {
    font-weight: 600;
    font-size: 38px;
    line-height: 44px;
  }
}
.sys-cover__sub-title {
  font-family: 'Rubik', sans-serif;
  font-weight: 600;
  font-size: 28px;
  line-height: 28px;
  text-align: center;
  color: #000000;
}
@media (max-width: 600px) {
  .sys-cover__sub-title {
    font-weight: 500;
    font-size: 22px;
    line-height: 24px;
  }
}
